body {
  font-family: "Poppins", sans-serif;
}

.App {
  justify-content: center;
  display: flex;
  background: linear-gradient(0deg, #ffdee9 0%, #b5fffc 100%);
  color: white;
}
.main {
  min-height: calc(100vh - 14rem);
  display: flex;
  align-items: center;
  flex-direction: column;
  margin: 4.5rem auto;
  border-radius: 6px;
  margin-bottom: 0;
  background-color: #343541;
  margin-bottom: 10px;
  width: 1000px;
}
.chats {
  overflow: hidden;
  overflow-y: scroll;
  scroll-behavior: smooth;
  width: 100%;
  max-width: 70rem;
  height: calc(100vh - 17rem);
}

.chat {
  margin: 1rem;
  padding: 2rem 3rem;
  font-size: 1.2rem;
  display: flex;
  align-items: center;
  align-items: flex-start;
  text-align: justify;
  background-color: #41434f9c;
  border: 1px solid #808080;
  width: 920px;
}
.txt {
  width: 660px;
  word-wrap: break-word;
  margin-left: 30px;
}
.chatimg {
  object-fit: cover;
  width: 100px;
  height: 100px;
  transform: scale(0.8);
  margin-right: 2rem;
  border-radius: 0.5rem;
}

.bot {
  background: #444654;
  width: fit-content;
  border-radius: 0.5rem;
}
.chatsFooter {
  margin-top: auto;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.inp {
  padding: 0.5rem;
  /* background: #808080; */
  border: 1px solid #808080;
  display: flex;
  align-items: center;
  border-radius: 0.5rem;
  width: 550px;
  max-width: 100%;
  padding: 0;
  position: relative;
}

.inp > input,
.send {
  background: transparent;
  outline: none;
  padding: 1.25rem;
  border: none;
}
input {
  width: 500px;
}
.send {
  position: absolute;
  right: 10px;
}
.chatsFooter > p {
  margin: 2rem 0;
}
.text-footer {
  color: #bab3b3;
  font-size: 15px;
}

